			var diff = iden[0]+iden[1]
			var _this = this
			if(diff === "MS") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (Res) {
				    //     if (Res.confirm) {
				    //     	var mso_iden = iden
				    //     	_this.$http.post('/outRequest/soDelete', {mso_iden}).then(([err,res]) => {
								// if(res.data.signal === 0){
								// 	_this.outList = _this.outList.filter(item => !(item.mso_iden.includes(mso_iden)))
								// }
				    //     		uni.showToast({
				    //     			icon: 'none',
				    //     			position: 'bottom',
				    //     			title: res.data.message
				    //     		});
				    //     	})
				    //     } else if (Res.cancel) {
				            
				    //     }
				    }
				});
			} else if(diff === "PR") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (Res) {
				        if (Res.confirm) {
							var pr_iden = iden
							_this.$http.post('/purchaseRequest/prDelete', {pr_iden}).then(([err,res]) => {
								//删除列表中该单据信息
								if(res.data.signal === 0) {
									_this.purchaseList = _this.purchaseList.filter(item => !(item.rp_iden.includes(pr_iden)))
								}
								uni.showToast({
									icon: 'none',
									position: 'bottom',
									title: res.data.message
								});
							})
				        } else if (Res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "SO") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (Res) {
				        if (Res.confirm) {
							// var so_iden = iden
							// _this.$http.post('/sellRequest/soDelete', {so_iden}).then(([err,res]) => {
							// 	if(res.data.signal === 0) {
							// 		_this.sellList = _this.sellList.filter(item => !(item.so_iden.includes(so_iden)))
							// 	}
							// 	uni.showToast({
							// 		icon: 'none',
							// 		position: 'bottom',
							// 		title: res.data.message
							// 	});
							// })
				        } else if (Res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "ST") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (Res) {
				        if (Res.confirm) {
				    //     	var str_iden = iden
				    //     	_this.$http.post('/exchangeRequest/strDelete', {str_iden}).then(([err,res]) => {
				    //     		if(res.data.signal === 0) {
								// 	_this.exchangeList = _this.exchangeList.filter(item => !(item.str_iden.includes(str_iden)))
								// }
				    //     		uni.showToast({
				    //     			icon: 'none',
				    //     			position: 'bottom',
				    //     			title: res.data.message
				    //     		});
				    //     	})
				        } else if (Res.cancel) {
				            
				        }
				    }
				});
			}
		},
		//提交草稿
		commitOrder(iden) {
			var diff = iden[0]+iden[1]
			var _this = this
			if(diff === "MS") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (Res) {
				        if (Res.confirm) {
				        	
				        } else if (Res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "PR") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (Res) {
				       if (Res.confirm) {
							var pr_iden = iden
							_this.$http.post('/purchaseRequest/prdSubmit', {pr_iden}).then(([err,res]) => {
								//修改列表中该单据信息
								if(res.data.signal === 0) {
									for(var i =0; i<_this.purchaseList.length; i++) {
										if(_this.purchaseList[i].rp_iden === iden){
											_this.purchaseList[i].rp_status = '已审批'
											break
										}
									}
								}
								uni.showToast({
									icon: 'none',
									position: 'bottom',
									title: res.data.message
								});
							})
				       } else if (Res.cancel) {
				           
				       }
				    }
				});
			} else if(diff === "SO") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (Res) {
				        if (Res.confirm) {
				        	
				        } else if (Res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "ST") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (Res) {
				        if (Res.confirm) {
				        	
				        } else if (Res.cancel) {
				            
				        }
				    }
				});
			}
		},
		//编辑草稿
		editOrder(iden) {
			var diff = iden[0]+iden[1]
			if(diff === "MS") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "PR") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "SO") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "ST") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			}
		},
		//关闭订单
		closeOrder(iden) {
			this.showHide = true
			this.closeIden = iden
		},
		clear1() {
			this.outFilterText = ''
		},
		clear2() {
			this.purchaseFilterText = ''
		},
		clear3() {
			this.sellFilterText = ''
		},
		clear4() {
			this.exchangeFilterText = ''
		},
		//隐藏输入框
		modelHide(){
			this.showHide=false
		},
		// 输入框确定按钮
		confirm(){
			var _this = this
			var myinfo = uni.getStorageSync('user_info')
			uni.showModal({
			    title: '提示',
			    content: '确认关闭请购单：'+_this.closeIden+" ?",
			    success: function (Res) {
			        if (Res.confirm) {
						var msg = {}
						msg.pr_iden = _this.closeIden
						msg.pr_closer = myinfo.data.user.user_name
						msg.pr_closerReason = _this.closeReason
						_this.$http.post('/purchaseRequest/prClose', msg).then(([err,res]) => {
							//修改列表中该单据信息
							if(res.data.signal === 0) {
								for(var i =0; i<_this.purchaseList.length; i++) {
									if(_this.purchaseList[i].rp_iden === _this.closeIden){
										_this.purchaseList[i].rp_status = '已关闭'
										break
									}
								}
							}
							uni.showToast({
								icon: 'none',
								position: 'bottom',
								title: res.data.message
							});
						})
						//清空缓存
						_this.closeIden = ''
						_this.closeReason = ''
			        } else if (Res.cancel) {
			            _this.closeIden = ''
			            _this.closeReason = ''
			        }
			    }
			});
			this.showHide=false
		},
		//判断权限
		judgePower() {
			var myinfo = uni.getStorageSync('user_info')
			var power = ['0','0','0','0']
			for(var i=0; i<myinfo.data.roles.length; i++){
				var po = myinfo.data.roles[i][1]
				power[0] = power[0]<po[6]? po[6]:power[0]
				power[1] = power[1]<po[1]? po[1]:power[0]
				power[2] = power[2]<po[0]? po[0]:power[0]
				power[3] = power[3]<po[9]? po[9]:power[0]
			}
			uni.setStorageSync('power',power)
		}
	},
	
	onLoad: function() {   
		//登录检查，需要重写一下
		// loginMsg = this.checkLogin('../pages/main/main', 'switchTab');
		// if(!loginMsg){
		// 	return;
		// }
		
		//填入权限
		this.judgePower()
		var _this = this
		var myinfo = uni.getStorageSync('user_info')
		var power = uni.getStorageSync('power')
		//判断界面权限
		if((power[2] === '0')&&(power[3] === '0')){
			this.items = ['出库','请购']
		} else {
			this.items = ['出库','请购','销售','转库']
		}
		
		
		//TODO 主页加载时默认搜索出库单
		// var outMes = {}
		// outMes.area_name = myinfo.data.user.area_name
		// outMes.user_now_iden = myinfo.data.user.username
		// outMes.power = power[0]
		// this.$http.post('/purchaseRequest/prs', outMes).then(([err,res]) => {
		// 	if (res.data.signal === '0') {
		// 		_this.testList = res.data.prs
		//     } else {
		// 		console.log(res.data.message)
		//     }
		// })
		
		
		uni.removeStorageSync('viewout');
		uni.removeStorageSync('viewpurchase');
		uni.removeStorageSync('viewsell');
		uni.removeStorageSync('viewexchange');
	}
}

